@import url('https://fonts.googleapis.com/css?family=Montserrat:300&display=swap');

.font {
  font-family: 'Montserrat', sans-serif;
}

h1 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 3em;
}

h2 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 1em;
}

.hover-card {
  font-family: 'Montserrat', sans-serif;
  transition: all .15s ease-in-out;
}

.hover-card:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.label {
  font-family: 'Montserrat', sans-serif;
}

.value-display {
  padding: 10px 0px;
  text-align: center;
}
